import React , { Component } from 'react';
import VidewStript1  from './videoStript';



class Stript extends Component
{
    state = {
        scrollTop:0
    };
    
   
    componentDidMount(){
        window.addEventListener('scroll', () => this.listenEvent(this.state));  
    };
    

   listenEvent(state) {
    const position = window.pageYOffset;
    this.setState({scrollTop:position});
   };

    
   /*
    scrollPosition(){
        return this.state.scrollTop <= 325 ? "stript2" : "stript1";
    };
  */
    scrollPosition(){
        
        return this.state.scrollTop <= 200 ? "stript2" : "stript1";
    };

    render(){

        return(
            <div style={{height:110}} className={this.scrollPosition()}>
                <VidewStript1 height={this.state.scrollTop} />
                    <h1 className="imgTitle" style={{marginLeft:200,position:"absolute",color:'white',fontSize:160}}><strong>Automotive</strong></h1>
            </div>
        );
    };
}


export default Stript;